body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  color: $text-color;
}

a {
  color: $link-color;
  text-decoration: underline;
  transition: color ease 0.3s;

  &:hover {
    color: inherit;
  }
}

h1 {
  font-size: 40px;
  font-weight: bold;
  color: $color-header-regiister;
}

.text-small {
  font-size: 12px;
  color: $color-text-register;
}

.btn {
  background: $btn-bg-color;
  color: $main-font;
  padding: 5px 29px;
  font-size: 14px;
  border: 0;
  border-radius: 0;
  white-space: nowrap;
}

.btn-lg {
  font-size: 20px;
  font-weight: 700;
  padding: 14px 47px;
}

.btn-secondary {
  background-color: $hr-color;
}

.btn-primary {
  background-color: $btn-bg-color;

  &:active,
  &:hover,
  &:not(:disabled):not(.disabled):active {
    background-color: darken($btn-bg-color, 20%);
  }
}

.btn-step {
  display: flex;
  justify-content: center;
  button {
    border: 1px solid #4db173;
    border-radius: 15px;
    padding: 66px 37px 15px 37px;
    border-top: none;
    margin-top: -50px;
    color: #4db173;
    font-size: 14px;
    font-weight: 700px;
  }
}

.text-h2 {
  font-size: 30px;
  font-weight: 700;
  color: #010101;
  text-align: center;
  margin: 40px 0 20px 0;
}

.register-info {
  text-align: center;
  margin: auto;
  font-size: 20px;
  color: $color-text-register;
  max-width: 511px;
  padding-bottom: 30px;
}

form {
  label.control-label {
    display: block;
    margin-bottom: 4px;
  }

  input.form-control {
    border: 2px solid $btn-bg-color;
    height: 36px;
    border-radius: 0;
  }
}

.page-heading {
  padding: 64px 0 69px;
  background-image: linear-gradient(
    to right,
    $gradient-left-color,
    $gradient-right-color
  );
  color: $main-font;
  text-align: center;

  .page-heading-inner {
    max-width: 750px;
    margin: auto;

    h1 {
      font-size: 40px;
      color: inherit;
    }

    p {
      font-size: 20px;
    }
  }
}

.my-tooptip {
  background-color: $btn-bg-dark;
  padding: 11px 13px;
  border-radius: 4px;
  color: $main-font;
  margin-top: 28px;
  font-size: 12px;
  position: relative;

  &:before {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;

    border-right: 16px solid $btn-bg-dark;
    left: -16px;
  }
}

.step {
  margin-bottom: 105px;
  .form-step {
    margin-bottom: 40px;
  }
}

.step-p {
  color: $color-text-register;
  font-size: 12px;
}
// TABS
.nav.nav-tabs {
  border-color: $footer-border;

  .nav-link {
    text-decoration: none;
    background-color: $nav-link-bg;
    color: $text-color;
    border-radius: 0;
    margin: 4px 6px;
    padding: 8px 33px;

    &.active {
      padding: 12px 39px;
      color: $gradient-right-color;
      border-color: $footer-border $footer-border $main-font;
      border-radius: 0;
      background-color: $main-font;
      margin: 0;
    }
  }
}
